exports['kotlin+coroutines/README.md'] = Handlebars.template({"compiler":[8,">= 4.3.0"],"main":function(container,depth0,helpers,partials,data) {
    var stack1, lookupProperty = container.lookupProperty || function(parent, propertyName) {
        if (Object.prototype.hasOwnProperty.call(parent, propertyName)) {
          return parent[propertyName];
        }
        return undefined
    };

  return "# "
    + ((stack1 = container.lambda(((stack1 = (depth0 != null ? lookupProperty(depth0,"metadata") : depth0)) != null ? lookupProperty(stack1,"name") : stack1), depth0)) != null ? stack1 : "")
    + "\n\nA movie getRating REST application written in [Kotlin](https://kotlinlang.org/) to demonstrate how it can Kotlin\ncoroutines can be used with Vert.x.  \n\nCoroutines enables to write asynchronous code with sequential statements, the function `rateMovie` is the best example\nshowing two SQL client operations wrapped in a `use` block managing the SQL connection.\n\n## Running from the IDE\n\nRun the main function from the IDE\n\n## Running as a far jar\n\n```\n> mvn package\n> java -jar target/kotlin-coroutines-examples.jar\n```\n\n## Running from the CLI\n\nCopy hsqldb jar to $VERTX_HOME/lib\n\n```\n> cp $HOME/.m2/repository/org/hsqldb/hsqldb/2.4.0/hsqldb-2.4.0.jar $VERTX_HOME/lib/\n> vertx run src/main/kotlin/movierating/App.kt\n```\n\n## API\n\nThe application exposes a REST API for getRating movies:\n\nYou can know more about a movie\n\n```\n> curl http://localhost:8080/movie/starwars\n{\"id\":\"starwars\",\"title\":\"Star Wars\"}\n```\n\nYou can get the current getRating of a movie:\n\n```\n> curl http://localhost:8080/getRating/indianajones\n{\"id\":\"indianajones\",\"getRating\":5}\n```\n\nFinally you can rateMovie a movie\n\n```\n> curl -X POST http://localhost:8080/rateMovie/starwars?getRating=4\n```\n";
},"useData":true})
exports['lambda+aws/README.md'] = Handlebars.template({"compiler":[8,">= 4.3.0"],"main":function(container,depth0,helpers,partials,data) {
    return "# aws-lambda-vertx-native\n\n### Custom Vert.x Native for AWS Lambda\n\n*Disclaimer - This project should be considered a POC and has not been tested or verified for production use.\nIf you decided to run this on production systems you do so at your own risk.*\n\n### Building this Runtime\n\n\n#### Prerequisites\n\nMake sure you have the following installed on your build machine before getting started.\n\n* GraalVM\n* AWS CLI\n\n##### Compile the Runtime Classes\n\n```\n$ ./mvnw package\n```\n\nIf your system cannot build working images (say to mismatch of library versions), you can build under docker.\n\n### Deploying to AWS Lambda\n\n#### Create a lambda role\n\n```\naws iam create-role \\\n    --role-name lambda-role \\\n    --path \"/service-role/\" \\\n    --assume-role-policy-document file:///tmp/trust-policy.json\n```\n\nWhere the file `/tmp/trust-policy.json` contains:\n\n```json\n{\n  \"Version\": \"2012-10-17\",\n  \"Statement\": [\n    {\n      \"Effect\": \"Allow\",\n      \"Principal\": {\n        \"Service\": \"lambda.amazonaws.com\"\n      },\n      \"Action\": \"sts:AssumeRole\"\n    }\n  ]\n}\n```\n\n#### Create a function\n\n```\naws lambda delete-function --function-name vertxNativeTester\n\naws lambda create-function --function-name vertxNativeTester \\\n    --zip-file fileb://target/lambda-0.0.1-SNAPSHOT-function.zip --handler lambda.ApplicationLoadBalancerLambda --runtime provided \\\n    --role arn:aws:iam::985727241951:role/service-role/lambda-role\n```\n\n#### Test it\n\n```\naws lambda invoke --function-name vertxNativeTester  --payload '{\"message\":\"Hello World\"}' --log-type Tail response.txt | grep \"LogResult\"| awk -F'\"' '{print $4}' | base64 --decode\n```\n";
},"useData":true})
